import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ModalController,ViewController } from 'ionic-angular';

/**
 * Generated class for the LocationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({name:"Location"})
@Component({
  selector: 'page-location',
  templateUrl: 'location.html',
})



export class LocationPage {

areas:any;
buttonColor:any;
selectedCity:any;
selectedArea:any;
textcolor:any;
cities:any=["Hyderabad","Bangalore","Mumbai","Chennai","Pune"]
cityArea:any= {
      "Hyderabad": [
          {'area':'Madhapur'},
          {"area":"Hitech-City"},
          {"area":"Kondapur"},
          {"area":"Gachibowli"}
      ],
      "Bangalore": [
          {"area":"Electronic-city"},
          {"area":"Marathahalli"},
          {"area":"GS Palya"}
      ],
      "Mumbai": [
        {"area":"Mumba-Electronic-city"},
        {"area":"Mumba-Marathahalli"},
        {"area":"Mumba-GS Palya"}
    ],
    "Chennai": [
      {"area":"Chennai-Electronic-city"},
      {"area":"Chennai-Marathahalli"},
      {"area":"Chennai-GS Palya"}
  ],
  "Pune": [
    {"area":"Pune-Electronic-city"},
    {"area":"Pune-Marathahalli"},
    {"area":"Pune-GS Palya"}
]
};

  constructor(public navCtrl: NavController,public navParams: NavParams,public modalCtrl: ModalController,public viewCtrl: ViewController) {
  }

  showAllAreas(event,city){
    this.buttonColor = '#440499';
    this.selectedCity=city;
    this.areas=this.cityArea[city];
  }
  showAllLocation(event,area){

    this.buttonColor = '#440499';
    this.textcolor = '#fff';
    this.selectedArea=area;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LocationPage');
  }

  dismissLocationModal() {
    this.viewCtrl.dismiss();
  }

}



